<template>
    <header class="fixed top-0 z-40 w-full h-10 bg-gradient-to-b bg-slate-600 md:respHeader">
        <div class="flex justify-around md:flex-col md:mt-16">
            <a href="#top" class="block text-md  font-semibold py-2 text-center transition duration-500 md:text-left" :class="[refView=='top' ? 'text-slate-200 md:-translate-x-1': 'text-slate-400']">
                Top
            </a>
            <a href="#profile" class="block text-md  font-semibold py-2 text-center transition duration-500 md:text-left" :class="[refView=='profile' ? 'text-slate-200 md:-translate-x-1': 'text-slate-400']">
                Profile
            </a>
            <a href="#skill" class="block text-md  font-semibold py-2 text-center transition duration-500 md:text-left" :class="[refView=='skill' ? 'text-slate-200 md:-translate-x-1': 'text-slate-400']">
                Skill
            </a>
            <a href="#works" class="block text-md  font-semibold py-2 text-center transition duration-500 md:text-left" :class="[refView=='works' ? 'text-slate-200 md:-translate-x-1': 'text-slate-400']">
                Works
            </a>
        </div>
    </header>
</template>

<script setup>

    import { ref, onMounted } from 'vue';

    const refView = ref("top");

    let headScroll_flag = true;

    // let top_elm;
    let profile_elm;
    let skill_elm;
    let works_elm;

    onMounted(() => {
        // top_elm = document.getElementById("top");
        profile_elm = document.getElementById("profile");
        skill_elm = document.getElementById("skill");
        works_elm = document.getElementById("works");
    });
    
    window.addEventListener("scroll", () => {
        if(headScroll_flag == false) {
            return;
        }

        headScroll_flag = false;
        let timeOut = setTimeout(() => {
            headScroll_flag = true;
            clearTimeout(timeOut);
        }, 200);

        // let top_y = top_elm.getBoundingClientRect().top;
        let profile_y = profile_elm.getBoundingClientRect().top;
        let skill_y = skill_elm.getBoundingClientRect().top;
        let works_y = works_elm.getBoundingClientRect().top;

        refView.value = "top"

        if(profile_y <= 200) {
            refView.value = "profile";
        }
        if(skill_y <= 200) {
            refView.value = "skill";
        }
        if(works_y <= 200) {
            refView.value = "works";
        }
    })

    

</script>

