<template>
    <article class="" id="skill">
        <h2 class="prmryHeading">SKILL</h2>

        <section class="mt-10">
            <h3 class="scdryHeading">言語・技術</h3>

            <section class="flex mb-10" v-for="item in skillInfo_arr" :key="item.lang">
                <img class="w-5 h-5 mr-3 mt-1" :src=item.img :alt=item.lang>
                <div>
                    <h4 class="font-semibold text-lg mb-4">{{ item.lang }}</h4>

                    <h5 class="mb-2 text-sm">学習期間</h5>
                    <p v-show="item.drill != 0" class="text-xs">{{ ((item.lang == "Java") ? "研修 " : "職業訓練 ") + item.drill }}ヶ月</p>
                    <div v-show="item.drill != 0" class="learnGraph h-3 max-w-0 transition-all duration-500 bg-gradient-to-br from-cyan-200 to-cyan-500 shadow-md" :style="{'width': item.drill + 'rem'}"></div>
                    <p class="text-xs mt-2">独習  {{ (item.self < 12) ? item.self + "ヶ月" : "１年以上"}}</p>
                    <div class="selfGraph h-3 max-w-0 transition-all duration-500 bg-gradient-to-br from-teal-200 to-teal-500 shadow-md" :style="{'width': item.self + 'rem'}"></div>

                    <ul class="list-inside mt-4 pl-0">
                        <li class="mb-1" v-for="text in item.text" :key="text">
                            {{ text }}
                        </li>
                    </ul>
                    
                </div>

            </section>


        </section>

        <section class="relative mt-12">
            <h3 class="scdryHeading">開発環境</h3>
            <div id="toolList" class="flex" @click.stop="setTip">
                <button 
                    v-for="item in tools_arr" :key=item.name @click="() => handleToolBtn(item.para)"
                    class="relative w-10 h-10 p-1 mr-4 box-content bg-gradient-to-br from-slate-100 to-slate-200 border border-slate-500 rounded"
                >
                    <img :src=item.img :alt=item.name class="w-8 h-8 m-auto">
                </button>
            </div>
            
            <p class="toolTip absolute z-10 left-2 mt-4 px-2 py-1 text-left w-fit max-w-full h-fit whitespace-pre bg-slate-50 border border-slate-500 rounded-sm text-sm" v-show="tipText!=''">{{ tipText }}</p>

        </section>

    </article>
</template>

<script setup>

    import {ref, onMounted} from 'vue';
    
    const tipText = ref("");

    const skillInfo_arr = [
        {
            lang: "HTML",
            img: require('@/assets/html5.svg'),
            drill: 4,
            self: 3,
            text:[
                "JavaScriptと連動したUIパーツの実装"
            ],
        },
        {
            lang: "CSS",
            img: require('@/assets/css3.svg'),
            drill: 4,
            self: 3,
            text:[
                "フォントサイズや背景画像の指定",
                "メディアクエリによるレスポンシブ化",
                "CSSフレームワークの利用"
            ]
        },
        {
            lang: "JavaScript",
            img: require('@/assets/javascript.svg'),
            drill: 3,
            self: 6,
            text:[
                "イベントリスナーの登録",
                "DOM要素の操作",
                "Fetch APIを用いたJSONデータの取得"
            ]
        },
        {
            lang: "Vue.js",
            img: require('@/assets/vue.svg'),
            drill: 2,
            self: 6,
            text:[
                "データバインディングを用いた表示内容の制御",
                "ライフサイクルフックを意識した処理の実装",
                "Composition APIとOptions APIの双方を扱った経験"
            ]
        },
        {
            lang: "React.js",
            img: require('@/assets/react.svg'),
            drill: 0,
            self: 2,
            text:[
                "JSX構文のマークアップと再利用",
                "",
            ]
        },
        {
            lang: "Java",
            img: require('@/assets/java.svg'),
            drill: 2,
            self: 12,
            text:[
                "マルチスレッドの実装",
                "二分探索や深さ優先探索、Union-Find等アルゴリズムの活用",
            ]
        }
    ];

    const tools_arr = [
        {
            name: "Windows",
            img: require("../assets/windows.svg"),
            para: "普段利用しているPCのOSです"
        },
        {
            name: "Mac OS",
            img: require("../assets/mac.svg"),
            para: "訓練校で利用していたOSです"
        },
        {
            name: "Visual Studio Code",
            img: require("../assets/vscode.svg"),
            para: "コーディングは主にこのエディタを使っています\n必要に応じて拡張機能を導入しています"
        },
        {
            name: "nodeJS",
            img: require("../assets/nodeJS.svg"),
            para: "npmを経由して外部ライブラリを取得しています"
        },
    ];

    function handleToolBtn(tipVal) {
        tipText.value = tipVal;
    }


    let skillScroll_flag = true;

    onMounted(() => {

        window.addEventListener("click", () => {
            tipText.value = "";
        });

        
        let learnGraph_elm = document.getElementsByClassName("learnGraph");
        let selfGraph_elm = document.getElementsByClassName("selfGraph");
        
        window.addEventListener("scroll", () => {
            
            if(skillScroll_flag == false) {
                return;
            }
            skillScroll_flag = false;
            let timeOut = setTimeout(() => {
                skillScroll_flag = true;
                clearTimeout(timeOut);
            }, 100);

            let screen_H = document.documentElement.clientHeight;

            for(let i = 0; i < selfGraph_elm.length; i++) {
                let graph_Y = selfGraph_elm[i].getBoundingClientRect().y;
                if(graph_Y <= (screen_H * 0.6)) {
                    learnGraph_elm[i].style.maxWidth = 100 + "vw";
                    selfGraph_elm[i].style.maxWidth = 100 + "vw";
                }

            }

        });

    });


</script>

<style>
    /* -------------------------------------------- */
    /* FOR MOBILE FIRST */
    /* ---------------------------------------------*/



    .skill__tool__icon {
        width: 1.8rem;
        margin-right: 1rem;
        box-sizing: content-box;
        padding: 0.4rem;
        border: 1px solid gray;
        border-radius: 0.5rem;
    }
    .skill__tool__toolTip {
        position: absolute;
        width: fit-content;
        max-width: 90%;
        margin-top: 15px;
        padding: 0.5rem 1rem;
        border: gray 1px solid;
        border-radius: 0.5rem;
        background-color: white;
        box-shadow: 1px 1px 3px gray;
        /* white-space: nowrap; */
    }
    .skill__tool__toolTip p {
        /* word-break: break-all; */
        font-size: 0.9rem;
    }


    /* -------------------------------------------- */
    /* FOR TABLET */
    /* -------------------------------------------- */
    @media screen and (min-width:768px) and ( max-width:1024px) {
        .skill__heading {
            padding-top: 45px;
        }
    }

    /* -------------------------------------------- */
    /* FOR PC */
    /* -------------------------------------------- */
    @media screen and (min-width:1024px) {
        .skill__heading {
            padding-top: 45px;
        }
    }
</style>
